import uniPagination from '@dcloudio/uni-ui/lib/uni-pagination/uni-pagination.vue'
	export default {
		data() {
			return {
			pageNum:5,
			currentPage:1,
			SearchInput:"",
			total:0,
			searchList:[],
			list:[],
			branch:0,
			pageSize:10
			}  
		},
		onLoad(option) { 
			var _self =this
			_self.getInfo()
		},
		onPullDownRefresh:function(){
			var _self = this
			_self.getInfo()
		},
		onShow(){
			var _self =this
			_self.getInfo()
		},
		methods: {
			change(e){
				console.log(e)
			},

			changePage(type){
				//console.log(type.current)
				this.currentPage = type.current
				this.getInfo()
			},
			getInfo:function(){
				var _self =this
				 uni.request({
				 	url:_self.$api+"dockingManager/cardQuery",
					data:{optionId:uni.getStorageSync("openId"),branch:_self.currentPage,live:_self.pageSize,id:0},
					method:"GET",
					success:function(res){
						console.log(res)
						if(res.data == ""){
							_self.branch = []
						}else{
							_self.branch = res.data[0].branch
						}
						_self.list = res.data  
						 uni.stopPullDownRefresh();
					}
				 })
			},
			goDetail:function(id){
				uni.navigateTo({
					url:"../auditContent/auditContent?id="+id
				})
			}
		},
		watch:{
			
		},
		filters:{
			rTime:function(data){
				if(data==null||data==undefined){
					return data
				}else{
					return data.slice(0,11)
				}
			}
		},
		components:{uniPagination}
	}
